span{
  color: #eee; 
}
.App {
  text-align: center;
  width: 100%; 
  height: 100%; 
  display: grid; 
  justify-content: center;
}
.input{ 
  width: 250px; 
  justify-self: center; 
  padding: 10px; 
}
.title{
  color: #333; 
}
.imgContent{
  display: grid; 
  justify-content: start;   
  background-color: #282c34;
  min-width: 350px;  
  margin: 30px; 
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  border-radius: 10px; 
  box-shadow: 0px 7px 2px 0px rgba(0, 0, 0, 0.2); 
  align-items: center;
  justify-items: center; 
  gap: 10px; 
  padding: 20px; 
}
.img{
   margin: 30px 30px;
   border-radius: 10px;   
   width: 270px;
   height: 330px;  
   object-fit: cover;
}
.notFound {
   color: #F00;
   margin-top: 20px;   
}
.text{
  color: #F00;
  font-size: 0.8rem; 
}
.btn{
  margin-top: 10px;
  padding: 10px 28px; 
  outline: none; 
  background-color: #fff;
  color: #282c34; 
  border: 2px solid #282c34;
  font-weight: 600;
}
.btn:hover{
   color: #fff; 
   background-color: #282c34;
   cursor: pointer; 
}
